@font-face {
  font-family: 'icomoon';
  src: url('../assets/fonts/icomoon.eot?ktxmzv');
  src: url('../assets/fonts/icomoon.eot?ktxmzv#iefix') format('embedded-opentype'),
    url('../assets/fonts/icomoon.ttf?ktxmzv') format('truetype'),
    url('../assets/fonts/icomoon.woff?ktxmzv') format('woff'),
    url('../assets/fonts/icomoon.svg?ktxmzv#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

@mixin icon-font {
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  speak: never;
}

[class^='icon-']:before,
[class*=' icon-']:before,
[class^='icon-']:after,
[class*=' icon-']:after {
  @include icon-font;
}

.icon-arrow-up:before {
  content: "\e900";
}

.icon-clock:before {
  content: "\e94e";
}

.icon-materials:before {
  content: "\e9b8";
}

.icon-user:before {
  content: "\e9dc";
}

.icon-share:before {
  content: "\ea82";
}

.icon-facebook:before {
  content: "\ea90";
}

.icon-twitter:before {
  content: "\ea96";
}

.icon-linkedin:before {
  content: "\eaca";
}

.icon-difficulty:before {
  content: "\eae8";
}